import { createStore } from 'vuex'
import axios from 'axios'
export default createStore({
  state: {
    user: '',
    usersInfo: null,
    repos: null
  },
  mutations: {
    setUser(state,payload) {
      state.user = payload
    },
    getUsersInfo(state,info) {
      state.usersInfo = info
      state.user = ''
    },
    getRepos(state,info){
      state.repos = info
    }
  },
  actions: {
    async getUsersInfo(context,user){
    try {
      let uinfo = await axios.get(`https://api.github.com/users/${user}`)
      context.commit('getUsersInfo', uinfo.data)
       let repos = await axios.get(`https://api.github.com/users/${user}/repos`)
       context.commit('getRepos', repos.data) 
    } catch (error) {
      console.error('Ошибка произошла при получении данных о юзере', error);
    }
    }
  },
  getters: {

  },
})
