<template>
   <nav class="nav__logo">
    <img src="@/assets/images/main.png" alt="" class="main">
   </nav>
    <div class="container">
       <div class="input">
        <input 
        type="text" 
        placeholder="Введите имя пользователя"
        class="search__input"
        v-model="getUser"
        >
        <button class="search__input-btn" @click="getUsersInfo(getUser)">Найти</button >
       </div>
      
       
    </div>
</template>

<script>
import { mapState,mapActions,mapMutations } from 'vuex'
    export default {
    computed: {
            ...mapState(['user']),
            getUser: {
                get() {
                    return this.user
                },
                set(val) {
                    this.setUser(val)
                }
            }
        },
        methods: {
            ...mapMutations(['setUser']),
            ...mapActions(['getUsersInfo'])
        },
    }
</script>

