<template>
  <div class="container" v-if="usersInfo">
    <div class="sort__overall">
      <h3 class="sort__main-texts">Сортировка</h3>
      <div class="sort__texts">
        <h4 class="sort__texts-name" @click="getReposN">ИМЯ</h4>
        <h4 class="sort__texts-star" @click="getReposS">ЗВЕЗДЫ</h4>
        <h4 class="sort__texts-data" @click="getReposD">ДАТА</h4>
      </div>
      <div class="sort__card">
        <div class="sort__info" v-for="repo in repos" :key="repo">
          <div class="sort__info-right">
            <div class="sort__info-name">{{ repo.name }}</div>
            <div class="sort__info-star">
              Кол-во звёзд: {{ repo.stargazers_count }}
            </div>
            <div class="sort__info-data">
              Дата добавления: {{ repo.pushed_at.slice(0, 10) }}
            </div>
          </div>
          <a :href="repo.html_url" target="_blank" class="sort__info-left-btn">ПОСЕТИТЬ</a>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  computed: {
    ...mapState(["usersInfo"]),
    ...mapState(["repos"]),
  },
  methods: {
    ...mapActions(["getRepos"]),
    getReposN() {
      this.repos.sort(function (a, b) {
        if (a.name > b.name) {
          return 1;
        }
        if (a.name < b.name) {
          return -1;
        }
        return 0;
      });
    },
    getReposS() {

      this.repos.sort(function (a, b) {
        return b.stargazers_count - a.stargazers_count;
      });
    },
    getReposD() {
      this.repos.sort(function (a, b) {
        //return a.created_at.slice(0, 10) - b.created_at.slice(0, 10);
        if (a.created_at.slice(0, 10) > b.created_at.slice(0, 10)) {
          return 1;
        }
        if (a.created_at.slice(0, 10) < b.created_at.slice(0, 10)) {
          return -1;
        }
        return 0;
      });
    },
  },
  created() { },
};
</script>