<template>
    <div class="user__info" v-if="usersInfo">
        <div class="user__info-right">
            <div class="user__info-img">
                <img :src="usersInfo.avatar_url" alt="">
            </div>
                
<a :href="usersInfo.html_url" target="_blank" class="user__info-right-btn" @click="getUsersInfo(usersInfo.html_url)">ПОСЕТИТЬ</a>

        </div>
        <div class="user__info-left">
        <h5 class="user__info-left-text">Логин:{{ usersInfo.login }}</h5>
        <h5 class="user__info-left-text">Репозиториев:{{ usersInfo.public_repos }}</h5>
        <h5 class="user__info-left-text">Создан:{{ usersInfo.created_at.slice(0, 10) }}</h5>
        <h5 class="user__info-left-text">Подписщиков:{{ usersInfo.followers }}</h5>
        <h5 class="user__info-left-text">Подписок:{{ usersInfo.following }}</h5>
        </div>
      </div>
</template>

<script>
import {mapState,mapActions} from 'vuex'
export default {
    computed: {
        ...mapState(['usersInfo', 'repos']),
        },
         methods: {
            ...mapActions(['getUsersInfo'])
        },
    }
</script>

